@import url(https://fonts.googleapis.com/css2?family=Satisfy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Caveat&display=swap);
html {
  scroll-behavior: smooth;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  width: 100%;
  height: 100%;
  margin: 0%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-family: "Satisfy", cursive;
  overflow-x: hidden;
}

.navigation {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  position: relative;
  width: 100%;
  height: 10%;
  padding-top: 2%;
  padding-bottom: 2%;
  padding-left: 6%;
  padding-right: 6%;
  color: black;
  background-color: #ffffff;
}

.nav-items {
  font-weight: bold;
  font-size: 1.2rem;
  color: rgb(160, 27, 104);
}
a {
  font-weight: bold;
  font-size: 1.2rem;
  text-decoration: none;
  color: black;
}
a:visited {
  text-decoration: none;
  color: black;
}
a:hover {
  text-decoration: none;
  color: rgb(160, 27, 104);
}
a:active {
  text-decoration: none;
  color: rgb(160, 27, 104);
}

.name-date {
  position: relative;
  width: 100%;
  height: 30%;
  background-color: white;
  padding-top: 2%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-image: url(/static/media/alro_img.7a56bd77.jpg);
  background-position: center;
  padding-bottom: 30%;
  background-size: cover;
  font-size: 1rem;
}

.banner__content {
  color: black;
}

.schedule-haldi {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  width: 100%;
  height: 40%;
  /* background-color: rgb(228, 203, 121); */
  background-image: url(/static/media/yellow.ff7f3896.jpg);
  background-size: cover;
  color: #815800;
  padding-top: 2%;
  padding-bottom: 2%;
}

.schedule__haldiContent {
  width: 90%;
  margin: auto;
}

.peacock-message {
  width: 90%;
  height: 20%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.peacock-img {
  width: 20%;
  padding-top: 2%;
  max-height: 20%;
}

.message {
  position: relative;
  width: 90%;
  height: 20%;
  padding-top: 3%;
  padding-bottom: 2%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  font-size: large;
}

.message .msg-body {
  padding-top: 2%;
  padding-bottom: 2%;
}

.hashtags {
  font-family: "Caveat", cursive;
  font-weight: bold;
  font-size: 1.2rem;
  width: 90%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  color: gray;
}

.countdown {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 90%;
  height: 100%;
}

#demo {
  text-align: center;
  font-size: 37px;
  color: rgb(105, 101, 101);
}

.schedule-sangeet {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  width: 100%;
  height: 40%;
  /* background-color: rgb(183, 228, 208); */
  background-image: url(/static/media/colorful.3fdb0325.jpg);
  background-size: cover;
  color: rgb(5, 51, 26);
  padding-top: 1%;
  padding-bottom: 2%;
}

.schedule__sangeetContent {
  width: 90%;
  margin: auto;
}

.schedule-wedding {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  width: 100%;
  height: 40%;
  background-color: rgb(160, 27, 104);
  /* background-image: url(images/wedding.jpg);
    background-size: cover; */
  color: rgb(223, 202, 139);
  padding-top: 2%;
