.rsvp {
  width: 100%;
  height: 50%;
  padding-top: 2%;
  padding-bottom: 3%;
}

.rsvp-msg {
  padding-top: 2%;
  padding-bottom: 3%;
}
.rsvp-btn {
  border-radius: 0.3rem;
  background-color: rgb(160, 27, 104);
  color: blanchedalmond;
  text-decoration: none;
  font: outline;
  cursor: pointer;
  height: 45px;
  width: 120px;
  text-align: center;
  font-weight: bold;
}
.spacing {
  padding: 1%;
}

.schedule__rsvpContent {
  width: 90%;
  margin: auto;
}

.contactus {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-weight: bold;
  position: relative;
  width: 100%;
  height: 40%;
  background-color: rgb(201, 226, 184);
}

.schedule__contactContent {
  width: 90%;
  margin: auto;
}

.rsvp__cover {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(100, 100, 100, 0.8);
}

.rsvp__loading {
  position: fixed;
  top: calc(50% - 20px);
  left: calc(50% - 20px);
}

.rsvp__form {
  position: fixed;
  left: calc(50% - 160px);
  top: calc(50% - 200px);
  width: 320px;
  height: 320px;
  background-color: white;
  border-radius: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.rsvp__title {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.rsvp__leftBuffer {
  width: 40px;
}

.rsvp__heading {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.rsvp__brideGroomSelection {
  width: 85%;
  -webkit-flex-wrap: nowrap !important;
          flex-wrap: nowrap !important;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom: 20px;
}

.rsvp__name {
  width: 85%;
  margin-bottom: 20px !important;
}

.rsvp__arrivalDate {
  width: 85%;
  margin-bottom: 20px !important;
}

.rsvp__submit {
  width: 85%;
  height: 40px;
}
